import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, AdaptivityProvider, AppRoot } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Data from './Data';

const App = () => {
	const [subjOpts, setSubjOpts] = useState([]);
	const [curSubj, setCurSubj] = useState(0);
	const [curFir, setCurFir] = useState(0);
	const [curSec, setCurSec] = useState(0);
	const [curSubjObj, setCurSubjObj] = useState(Data[0]);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		let ar = []
		for(let i = 0; i < Data.length; i++){
			ar.push(<option key={'subj'+i}>{Data[i].subject}</option>);
		}
		setSubjOpts(ar);
	}, []);
	
	const onInput = e => {
		setCurFir(e.target.value);
		setCurSec(calcScore(curSubj, e.target.value));
	};
	
	const onChange = e => {
		setCurSubj(e.target.selectedIndex);
		setCurSubjObj(Data[e.target.selectedIndex]);
		setCurSec(calcScore(e.target.selectedIndex, curFir));
	}
	
	const calcScore = (subjI, score) => {
		let subj = Data[subjI];
		if(score > 0 && score < subj.score.length) return subj.score[score]
		else return '';
	};

	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel='home'>
					<Home id='home' subjOpts={subjOpts} curFir={curFir} curSec={curSec} onInput={onInput} onChange={onChange} curSubj={curSubjObj} />
				</View>
			</AppRoot>
		</AdaptivityProvider>
	);
}

export default App;
