import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, Group, FormItem, Input, NativeSelect, Header, SimpleCell, InfoRow } from '@vkontakte/vkui';

const Home = ({ id, subjOpts, curFir, curSec, onInput, onChange, curSubj }) => {
	
	return (
		<Panel id='home'>
			<PanelHeader>Калькулятор ЕГЭ</PanelHeader>

			<Group>
				<FormItem top="Предмет">
					<NativeSelect onChange={onChange}>
						{subjOpts}
					</NativeSelect>
				</FormItem>
				<FormItem top="Первичный балл">
					<Input type="number" placeholder="0" onInput={onInput} />
				</FormItem>
				<FormItem top="Итоговый балл">
					<Input type="number" placeholder="0" value={curSec} readonly />
				</FormItem>
			</Group>
			<Group>
				<Header>{curSubj.subject}</Header>
					{curSubj.minAt && <SimpleCell>
						<InfoRow header="Минимальный балл для получения аттестата">{curSubj.minAt}</InfoRow>
					</SimpleCell>}
				<SimpleCell>
					<InfoRow header="Минимальный балл для поступления в ВУЗ">{curSubj.minUn}</InfoRow>
				</SimpleCell>
			</Group>
		</Panel>
	);
}

export default Home;
